








import Vue from "vue";
export default Vue.extend({
  data: () => ({
    weather: "weather",
    water: "water",
    power: "power",
  }),
  created() {
    this.$emit("selectedTab", 0);
  },
  methods: {
    tabChange(selected: Event) {
      console.log(selected);
      this.$emit("setTab", selected);
    },
  },
});
