







































import Vue from "vue";
import { mapState } from "vuex";
import Graph from "./Graph.vue";
import TempViewVue from "./TempView.vue";
import { WeatherCall, WaterCall, PowerCall } from "../../consts";
import * as models from "../../services/models";
import { WeatherFuncModel } from "../../services/models";
import WaterViewVue from "./WaterView.vue";
export default Vue.extend({
  props: ["dates", "tab"],
  data: () => ({
    value: [] as number[],
    labels: [] as string[],
    highs: [] as number[],
    lows: [] as number[],
    weatherData: {} as models.WeatherFuncModel,
    waterData: {} as models.WaterFuncModel,
    powerData: {} as models.PowerFuncModel,
    isLoading: false,
    month: "",
    callComplete: false,
  }),

  components: {
    graph: Graph,
    temp: TempViewVue,
    water: WaterViewVue,
  },
  computed: {
    ...mapState(["index"]),
  },
  methods: {
    initialize: WeatherCall,
    waterCall: WaterCall,
    powerCall: PowerCall,
  },
  created() {
    this.value = [] as number[];
    this.labels = [] as string[];
    this.highs = [] as number[];
    this.lows = [] as number[];
    this.weatherData = {} as models.WeatherFuncModel;
    this.waterData = {} as models.WaterFuncModel;
    this.powerData = {} as models.PowerFuncModel;
    switch (this.tab) {
      case 0:
        this.callComplete = false;
        this.isLoading = true;
        this.initialize(
          this.dates,
          this.value,
          this.labels,
          this.highs,
          this.lows
        )
          .then((weather) => {
            this.weatherData = weather;
            console.log(this.weatherData);
          })
          .catch((err) => console.log(err))
          .finally(() => {
            this.callComplete = true;
            this.isLoading = false;
          });
        break;
      case 1:
        this.isLoading = true;
        this.callComplete = false;
        this.waterCall(this.dates, this.value, this.labels)
          .then((waterResult) => (this.waterData = waterResult))
          .finally(() => {
            this.isLoading = false;
            this.callComplete = true;
          });
        break;
      case 2:
        this.isLoading = true;
        this.callComplete = false;
        this.powerCall(this.dates, this.value, this.labels)
          .then((powerResult) => {
            this.powerData = powerResult;
          })
          .finally(() => {
            this.isLoading = false;
            this.callComplete = true;
          });
          break;
    }
  },
  watch: {
    tab(newVal) {
      this.value = [] as number[];
      this.labels = [] as string[];
      this.highs = [] as number[];
      this.lows = [] as number[];
      this.weatherData = {} as models.WeatherFuncModel;
      this.waterData = {} as models.WaterFuncModel;
      this.powerData = {} as models.PowerFuncModel;
      switch (newVal) {
        case 0:
          this.callComplete = false;
          this.isLoading = true;
          this.initialize(
            this.dates,
            this.value,
            this.labels,
            this.highs,
            this.lows
          )
            .then((weather) => {
              this.weatherData = weather;
              console.log(this.weatherData);
            })
            .catch((err) => console.log(err))
            .finally(() => {
              this.callComplete = true;
              this.isLoading = false;
            });
          break;
        case 1:
          this.isLoading = true;
          this.callComplete = false;
          this.waterCall(this.dates, this.value, this.labels)
            .then((waterResult) => {
              this.waterData = waterResult;
              this.waterData.labels = this.labels;
              console.log(this.waterData);
            })
            .finally(() => {
              this.isLoading = false;
              this.callComplete = true;
            });
          break;
        case 2:
          this.isLoading = true;
          this.callComplete = false;
          this.powerCall(this.dates, this.value, this.labels)
            .then((powerResult) => {
              this.powerData = powerResult;
            })
            .finally(() => {
              this.isLoading = false;
              this.callComplete = true;
            });
            break;
        default:
          console.log("Tab is " + this.tab);
      }
    },
  },
});
